import endpoints from '../constants/endpoints';
import BaseService from './baseService';
import http from '../axios';

class RfScheduleService extends BaseService {
  constructor() {
    super(endpoints.rfSchedules);
  }

  patchSchedule(uuid, payload) {
    const url = endpoints.rfSchedules();
    const endpoint = `${url}/write/${uuid}`;
    return http.patch(endpoint, payload);
  }
}

export const rfScheduleService = new RfScheduleService();
